.representatives {
    padding-top: 40px;
    padding-bottom: 84px;
    &_filters {
        display: flex;
        justify-content: space-between;
        align-items: center;
        &-item {
            display: flex;
            align-items: center;
        }
        &-name {
            font-weight: bold;
            font-size: 20px;
            line-height: 126.4%;
            color: #6D788C;
            margin-right: 16px;
        }
        .input_block {
            position: relative;
            width: 307px;
            .input_block-fake_placeholder {
                position: absolute;
                top: 50%;
                left: 16px;
                transform: translate(0,-50%);
                z-index: 5;
            }
            .btn {
                position: absolute;
                top: 48px;
                right: 7px;
                transform: translate(0,-50%);
            }
            
        }
        .input_block-fieldset input {
            padding: 20px 85px 19px 80px;
        }
    }
}